.support-box{
  display: grid;
  max-width: 335px;
  height: 472px;
  padding-top: 24px;
  
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4F2EE8 18.03%, #FFFFFF 100%);
  box-shadow: 0px 10px 10px rgba(245, 245, 247, 0.1);
  border-radius: 16px;
  // justify-content: center;
  // align-items: center;
}

