.support-title{
  display: flex;
  padding-left: 40px;
  font-family: var(--primary-font);
  color: var(--title-text-color-theme-dark) ;
  font-weight: 700;
  margin-top: 16px;
  font-size: 28px;
  line-height: 1.2;
  letter-spacing: -0.04em;
  padding-bottom: 40px;
}

.support-list{
  padding-left: 40px;
}

.support-button{
  display: flex;
  width: 38px;
  height: 38px;
  background-color: var(--title-text-color-theme-dark);
  border-radius: 50%;
  border: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
//   transform: ;
}

.support-button:focus,
.support-button:hover{
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    // transform: rotate(180deg);
}
