.button-support{
    justify-content: center;
    justify-self: center;
    align-self: flex-end;
    margin-bottom: 24px;
}

.support-button-svg{
  width: 22px;
  height: 22px;
  fill:var(--accent-color);
  display: flex;
  margin: 0;
}

